import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	computed: mapState(['hasLogin', 'userInfo']),
	data() {
		return {
			chatList: [],
			isLogin: false,
			isFresh:false
		}
	},
	onShow() {
		console.log("onShow");
		var accessToken = uni.getStorageSync('userInfo').accessToken;
		if (accessToken) {
			this.getChatList();
			this.isLogin = true;
		} else {
		
		}
	},
	onTabItemTap(){
		var accessToken = uni.getStorageSync('userInfo').accessToken;
		if (accessToken) {
			this.getChatList();
			this.isLogin = true;
		} 
	},
	methods: {
		gotoChart(myId,toId) {
			console.log(myId+"   "+toId);
			const that = this;
			that.$Router.push({
				path: "/pages/consultant/charRoom",
				query:{
					myUserId:myId,
					chatUserId:toId
				}
			})
		},
		getChatList() {
			const that = this;
			that.$api.mychatList({}).then(res => {
				if (res.success) {
					if (that.isFresh) {
						setTimeout(() => {
							uni.stopPullDownRefresh();
						}, 1000)
						that.isFresh = false;
					}
					let result = res.datas.rows;
					for (var i in result) {
						result[i].chatUserHeadImg = result[i].chatUserHeadImg ? result[i].chatUserHeadImg :
							"../../static/images/defaultUserPic.png";
					}
					that.chatList = result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		onPullDownRefresh: function() {
			const that = this;
			that.getChatList();
			that.isFresh = true;
			setTimeout(() => {
				uni.stopPullDownRefresh();
			}, 10000)
		}
	}
}
